"use client";
import { useState } from 'react';
import Link from 'next/link';
import { useFinance } from '@/context/FinanceContext';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { ArrowLeft, Trash2, Plus, Save, Wallet, Building2, Banknote, CreditCard } from 'lucide-react';
import { Account, Category, AccountType } from '@/types';
import { v4 as uuidv4 } from 'uuid';

export default function AdminPage() {
  const { 
      accounts, categories, fixedRules, 
      addAccount, deleteAccount, updateAccount,
      addCategory, deleteCategory, addSubCategory, removeSubCategory,
      updateFixedRule, deleteFixedRule
  } = useFinance();

  // Estados locales para formularios nuevos
  const [newAccName, setNewAccName] = useState('');
  const [newAccBank, setNewAccBank] = useState('');
  const [newAccType, setNewAccType] = useState<AccountType>('bank');
  
  const [newCatName, setNewCatName] = useState('');
  const [newSubItem, setNewSubItem] = useState('');

  const handleAddAccount = () => {
      if(!newAccName) return;
      const newAcc: Account = {
          id: uuidv4(), name: newAccName, bank: newAccBank || 'Genérico', 
          type: newAccType, owner: 'Ambos', balance: 0, isHidden: false
      };
      addAccount(newAcc);
      setNewAccName(''); setNewAccBank('');
  };

  const handleAddCategory = () => {
      if(!newCatName) return;
      const newCat: Category = { id: uuidv4(), name: newCatName, type: 'expense', subCategories: [] };
      addCategory(newCat);
      setNewCatName('');
  };

  return (
    <div className="container mx-auto p-4 max-w-4xl space-y-6 pb-24">
      {/* Botón Volver */}
      <div className="flex items-center gap-4">
        <Link href="/">
            <Button variant="outline" size="icon" className="rounded-full border-zinc-700 hover:bg-zinc-800">
                <ArrowLeft className="h-5 w-5" />
            </Button>
        </Link>
        <h1 className="text-2xl font-bold">Configuración del Sistema</h1>
      </div>

      <Tabs defaultValue="categories" className="space-y-4">
        <TabsList className="bg-zinc-900 border border-zinc-800 w-full justify-start overflow-x-auto">
            <TabsTrigger value="categories">Categorías y Listas</TabsTrigger>
            <TabsTrigger value="accounts">Cuentas y Bancos</TabsTrigger>
            <TabsTrigger value="fixed">Gastos Recurrentes</TabsTrigger>
        </TabsList>

        {/* --- CATEGORÍAS (ACORDEÓN) --- */}
        <TabsContent value="categories">
            <Card className="bg-zinc-900 border-zinc-800">
                <CardHeader>
                    <CardTitle>Editor de Listas</CardTitle>
                    <CardDescription>Edita categorías principales y sus sub-items.</CardDescription>
                </CardHeader>
                <CardContent className="space-y-6">
                    {/* Agregar Nueva Categoría */}
                    <div className="flex gap-2">
                        <Input placeholder="Nombre Nueva Categoría" value={newCatName} onChange={e => setNewCatName(e.target.value)} className="bg-black" />
                        <Button onClick={handleAddCategory}><Plus className="mr-2 h-4 w-4"/> Crear</Button>
                    </div>

                    <Accordion type="single" collapsible className="w-full">
                        {categories.map(cat => (
                            <AccordionItem key={cat.id} value={cat.id} className="border-zinc-800">
                                <AccordionTrigger className="hover:no-underline px-2 hover:bg-zinc-800/50 rounded">
                                    <div className="flex items-center gap-2">
                                        <span className={`w-2 h-2 rounded-full ${cat.type === 'income' ? 'bg-green-500' : 'bg-red-500'}`} />
                                        {cat.name}
                                    </div>
                                </AccordionTrigger>
                                <AccordionContent className="p-4 bg-black/30 space-y-3">
                                    <div className="flex justify-between items-center mb-4">
                                        <p className="text-xs text-zinc-500">ID: {cat.id} - {cat.type.toUpperCase()}</p>
                                        <Button variant="destructive" size="sm" onClick={() => deleteCategory(cat.id)}>Eliminar Categoría</Button>
                                    </div>
                                    
                                    {/* Sub Items */}
                                    <div className="space-y-2">
                                        <Label>Items de la lista:</Label>
                                        <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                                            {cat.subCategories?.map(sub => (
                                                <div key={sub} className="flex justify-between items-center bg-zinc-900 p-2 rounded border border-zinc-800">
                                                    <span>{sub}</span>
                                                    <Button variant="ghost" size="icon" className="h-6 w-6 text-red-400" onClick={() => removeSubCategory(cat.id, sub)}>
                                                        <Trash2 className="h-3 w-3" />
                                                    </Button>
                                                </div>
                                            ))}
                                        </div>
                                        <div className="flex gap-2 mt-2">
                                            <Input placeholder="Nuevo item..." id={`input-${cat.id}`} className="h-8 bg-zinc-900" 
                                                   onKeyDown={(e) => {
                                                       if(e.key === 'Enter') {
                                                            addSubCategory(cat.id, e.currentTarget.value);
                                                            e.currentTarget.value = '';
                                                       }
                                                   }}
                                            />
                                            <Button size="sm" variant="secondary" onClick={() => {
                                                const el = document.getElementById(`input-${cat.id}`) as HTMLInputElement;
                                                if(el.value) { addSubCategory(cat.id, el.value); el.value=''; }
                                            }}>Add</Button>
                                        </div>
                                    </div>
                                </AccordionContent>
                            </AccordionItem>
                        ))}
                    </Accordion>
                </CardContent>
            </Card>
        </TabsContent>

        {/* --- CUENTAS --- */}
        <TabsContent value="accounts">
             <Card className="bg-zinc-900 border-zinc-800">
                <CardHeader><CardTitle>Gestión de Cuentas</CardTitle></CardHeader>
                <CardContent className="space-y-6">
                    {/* Crear Cuenta */}
                    <div className="grid grid-cols-1 md:grid-cols-4 gap-4 bg-zinc-950 p-4 rounded-lg border border-zinc-800">
                        <Input placeholder="Nombre (ej: Nequi Jhon)" value={newAccName} onChange={e => setNewAccName(e.target.value)} />
                        <Input placeholder="Banco (ej: Nequi)" value={newAccBank} onChange={e => setNewAccBank(e.target.value)} />
                        <Select value={newAccType} onValueChange={(v: AccountType) => setNewAccType(v)}>
                            <SelectTrigger><SelectValue /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="bank">Banco Tradicional</SelectItem>
                                <SelectItem value="wallet">Billetera Digital</SelectItem>
                                <SelectItem value="cash">Efectivo</SelectItem>
                                <SelectItem value="bond">Bonos/Vales</SelectItem>
                            </SelectContent>
                        </Select>
                        <Button onClick={handleAddAccount} className="bg-indigo-600"><Plus className="mr-2 h-4 w-4"/> Agregar</Button>
                    </div>

                    <div className="space-y-2">
                        {accounts.map(acc => (
                            <div key={acc.id} className="flex items-center justify-between p-3 bg-zinc-950/50 border border-zinc-800 rounded-lg">
                                <div className="flex items-center gap-3">
                                    <div className="p-2 bg-zinc-900 rounded-full text-zinc-400">
                                        {acc.type === 'wallet' ? <Wallet size={16}/> : acc.type === 'cash' ? <Banknote size={16}/> : acc.type === 'bond' ? <CreditCard size={16}/> : <Building2 size={16}/>}
                                    </div>
                                    <div>
                                        <p className="font-bold">{acc.name}</p>
                                        <p className="text-xs text-zinc-500">{acc.bank} • ${acc.balance.toLocaleString()}</p>
                                    </div>
                                </div>
                                <Button variant="ghost" size="icon" className="text-red-500 hover:bg-red-950/20" onClick={() => deleteAccount(acc.id)}>
                                    <Trash2 className="h-4 w-4" />
                                </Button>
                            </div>
                        ))}
                    </div>
                </CardContent>
             </Card>
        </TabsContent>

        {/* --- GASTOS RECURRENTES (EDITABLE) --- */}
        <TabsContent value="fixed">
            <div className="space-y-4">
            {fixedRules.map(rule => (
                <Card key={rule.id} className="bg-zinc-900 border-zinc-800">
                    <CardContent className="p-4 flex flex-col md:flex-row gap-4 items-end">
                        <div className="flex-1 space-y-2 w-full">
                            <Label>Descripción</Label>
                            <Input value={rule.description} onChange={(e) => updateFixedRule({...rule, description: e.target.value})} className="bg-black" />
                        </div>
                        <div className="w-24 space-y-2">
                            <Label>Día</Label>
                            <Input type="number" value={rule.dayOfMonth} onChange={(e) => updateFixedRule({...rule, dayOfMonth: parseInt(e.target.value)})} className="bg-black" />
                        </div>
                        <div className="w-32 space-y-2">
                            <Label>Valor</Label>
                            <Input type="number" value={rule.budgetedAmount} onChange={(e) => updateFixedRule({...rule, budgetedAmount: parseFloat(e.target.value)})} className="bg-black" />
                        </div>
                        <Button variant="destructive" size="icon" onClick={() => deleteFixedRule(rule.id)}><Trash2 className="h-4 w-4"/></Button>
                    </CardContent>
                </Card>
            ))}
            </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}